$black: black;
$gold: gold;
$white: white;
.navbar {
  background: $black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 22;
  
  .menu {
    color: $white;
    font-size: 1.4rem;
    padding: 0.6rem 5%;
    cursor: pointer;
    transition: 0.3s ease;
    &:hover {
      color: $gold;
    }
  }
  .nav2 {
    display: none;
  }
  nav {
    position: absolute;
    background: rgba(0, 0, 0, 0.3);
    width: 100%;
    height: 100vh;
    top: 70px;
    transition: 0.4s ease;
    ul {
      background: $white;
      width: 80%;
      height: 100vh;
      position: absolute;
      top: 0px;
      right: 0;
      li {
        cursor: pointer;
        margin: 1rem;
        padding: 0.7rem;
        position: relative;
        color: $black;
        font-weight: 500;
        text-transform: uppercase;
        transition: 0.3s;
        position: relative;
        &:hover {
          color: $gold;
        }
        &:hover ul {
          left: 15px;
          opacity: 1;
          visibility: visible;
        }
        ul {
          background: $gold;
          height: auto;
          width: 200px;
          margin-top: 30px;
          position: absolute;
          left: 0px;
          opacity: 0;
          visibility: hidden;
          li {
            padding: 0;
            line-height: 2;
            a {
              color: $black;
              &:hover {
                color: $white;
              }
            }
          }
        }
        a {
          color: $black;
          font-weight: 500;
          text-transform: uppercase;
          transition: 0.3s;
          line-height: 2;
          &:hover {
            color: $gold;
          }
        }
      }
      .action {
        margin-top: 3rem;
      }
      .btn {
        background: $gold;
        border: none;
        padding: 1rem 2rem;
        font-size: 1.1rem;
        font-weight: 500;
        display: block;
        margin: 1rem auto;
        width: 70%;
        cursor: pointer;
        text-align: center;
        text-transform: uppercase;
      }
      .btn1 {
        background: $black;
        color: $white;
        margin-bottom: 2rem;
      }
    }
  }
}

@media (min-width: 800px) {
  .navbar {
    h1 {
      font-size: 2rem;
      padding: 1rem 3rem;
    }
    .menu {
      display: none;
    }
    .nav2 {
      display: block;
      background: rgba(0, 0, 0, 0.3);
      transition: 0.4s ease;
      ul {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-right: 2rem;
        li {
          display: inline-block;
          cursor: pointer;
          color: $white;
          font-weight: 500;
          text-transform: uppercase;
          transition: 0.3s;
          position: relative;
          margin: 0 0.7rem;
          
          &:hover {
            color: $gold;
          }
          &:hover ul {
            bottom: -250%;
            opacity: 1;
            visibility: visible;
          }
          .sublist {
            background: $gold;
            height: auto;
            display: flex;
            align-items: flex-start;
            flex-direction: column;
            margin: 0px;
            padding: 0;
            position: absolute;
            z-index: 333;
            bottom: -100px;
            transition: .4s;
            opacity: 0;
            visibility: hidden;
            width: 130px;
            li {
              background-color: none;
              

              // padding: 0;
              // line-height: 2;
              // border-bottom: 1px solid $black;
              a {
                color: $black;
                &:hover {
                  color: $white;
                }
              }
            }
          }
          a {
            color: $white;
            font-weight: 600;
            text-transform: uppercase;
            transition: 0.3s;
            line-height: 2;
            &:hover {
              color: $gold;
            }
          }
        }
        .action {
          margin-top: 0rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          display: none;
        }
        .btn {
          background: $gold;
          border: none;
          padding: 0.2rem 0.5rem;
          font-size: 1rem;
          font-weight: 500;
          display: block;
          margin: 1rem auto;
          width: auto;
          cursor: pointer;
          text-align: center;
          text-transform: uppercase;
        }
        .btn1 {
          background: $black;
          color: $white;
        }
      }
    }
    nav {
      display: none;
      ul {
        display: none;
      }
    }
  }
}
