.hero {
  margin-top: 4rem;
}
.hero .swiper-pagination {
  top: 500px;
}
.hero .container2 {
  display: block;
  width: 100%;
  padding: 1rem 0 2rem;
}
.hero .container2 .texts {
  margin-left: 1rem;
}
.hero .container2 .texts .heading {
  font-size: 1.5rem;
  color: black;
  margin-bottom: 0.3rem;
  font-weight: bold;
}
.hero .container2 .texts p {
  font-size: 1.2rem;
  color: #333;
  padding: 0.5rem 0;
  text-transform: capitalize;
  font-weight: 500;
}
.hero .container2 .texts ul li {
  list-style-type: none;
  font-size: 0.8rem;
  padding: 0.2rem 2rem;
  position: relative;
}
.hero .container2 .texts ul li::before {
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 0%;
  transform: translate(50%, -50%);
  background: gold;
}
.hero .container2 .texts .btn {
  text-transform: uppercase;
  padding: 0.5rem 1.3rem;
  border: none;
  font-size: 1rem;
  background: gold;
}
.hero .container2 .images {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
}
.hero .container2 .images img {
  width: 90%;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
}

@media (min-width: 690px) {
  .hero {
    margin-top: 9rem;
  }
  .hero .container2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem 2rem;
  }
  .hero .container2 .texts {
    margin-left: 0rem;
  }
  .hero .container2 .texts .heading {
    font-size: 1.7rem;
  }
  .hero .container2 .texts p {
    font-size: 1rem;
  }
  .hero .container2 .images img {
    width: 300px;
  }
}
