.footer {
  margin-top: 3rem;
}
.footer .container2 {
  background: black;
}
.footer .container2 .list {
  display: flex;
  text-align: center;
  flex-direction: column;
}
.footer .container2 .list li {
  color: white;
  display: flex;
  flex-direction: column;
  margin: 1rem 0;
}
.footer .container2 .list li h5 {
  font-size: 1.1rem;
  padding: 0.6rem;
  color: gold;
  text-decoration: underline;
}
.footer .container2 .list li a {
  color: white;
  font-size: 1.1rem;
  text-decoration: none;
  margin: 0.5rem 0;
  transition: 0.3s ease;
}
.footer .container2 .list li a:hover {
  color: gold;
  text-decoration: underline;
}
.footer .container2 .list li p {
  font-size: 1.2rem;
  padding: 0.4rem;
}
.footer .warning {
  padding: 0 1rem;
  margin-bottom: 4rem;
}
.footer .warning h3 {
  color: #333;
  text-align: center;
  padding: 2rem 0 1rem;
  font-size: 1rem;
  text-transform: uppercase;
}
.footer .warning .mainred {
  color: rgba(255, 0, 0, 0.938);
  margin-bottom: 0.5rem;
}
.footer .warning p {
  font-size: 0.9rem;
  text-align: center;
  font-weight: bold;
  color: #333;
}

@media (min-width: 636px) {
  .footer .container2 .list {
    display: flex;
    text-align: left;
    justify-content: space-evenly;
    flex-direction: row;
  }
  .footer .container2 .list li {
    color: white;
    display: flex;
    flex-direction: column;
    margin: 1rem 3rem;
  }
  .footer .container2 .list li h5 {
    font-size: 1.2rem;
    padding: 0.6rem;
    color: gold;
    text-decoration: underline;
  }
  .footer .container2 .list li a {
    color: white;
    font-size: 1.1rem;
    text-decoration: none;
    margin: 0.5rem 0.6rem;
  }
  .footer .container2 .list li p {
    font-size: 1.2rem;
    padding: 0.4rem;
  }
}/*# sourceMappingURL=Footer.css.map */