$black: black;
$white: white;
$gold: gold;
$gray: #333;
.hero {
  margin-top: 4rem;
  .swiper-pagination {
    top: 500px;
  }
  .container2 {
    display: block;
    width: 100%;
    padding: 1rem 0 2rem;
    .texts {
      margin-left: 1rem;
      .heading {
        font-size: 1.5rem;
        color: $black;
        margin-bottom: 0.3rem;
        font-weight: bold;
      }
      p {
        font-size: 1.2rem;
        color: $gray;
        padding: 0.5rem 0;
        text-transform: capitalize;
        font-weight: 500;
      }
      ul {
        li {
          list-style-type: none;
          font-size: 0.8rem;
          padding: 0.2rem 2rem;
          position: relative;
          &::before {
            content: "";
            width: 10px;
            height: 10px;
            border-radius: 50%;
            position: absolute;
            top: 50%;
            left: 0%;
            transform: translate(50%, -50%);
            background: $gold;
          }
        }
      }
      .btn {
        text-transform: uppercase;
        padding: 0.5rem 1.3rem;
        border: none;
        font-size: 1rem;
        background: $gold;
      }
    }
    .images {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 1rem;
      img {
        width: 90%;
        height: auto;
        border-radius: 10px;
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
      }
    }
  }
}

@media (min-width:690px) {
  .hero {
    margin-top: 9rem;
    .container2 {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 1rem 2rem 2rem;
      .texts {
        margin-left: 0rem;
        .heading {
          font-size: 1.7rem;
        }
        p {
          font-size: 1rem;
        }
                
      }
      .images {
        img {
          width: 300px;
        }
      }
    }
  }
}
