* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.containerZ {
  margin-top: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  .images {
    padding: 2rem;
    img {
      width: 200px;
      height: auto;
    }
  }
  form {
    display: flex;
    flex-direction: column;
    width: 80%;
    .text {
      h1 {
        font-size: 2rem;
        padding: 0.5rem 0 0rem;
        text-align: center;
      }
      p {
        font-size: 1.3rem;
        margin-bottom: 2rem;
        color: #333;
        text-align: center;
      }
    }
    .label {
      font-size: 1.3rem;
      margin-top: 1rem;
      padding: 0.5rem 0;
    }
    .input {
      height: 3rem;
      background: rgb(230, 229, 229);
      border: none;
      outline: none;
      width: 100%;
      font-size: 1.2rem;
      padding: 0 1rem;
    }
    select {
      height: 3rem;
      background: rgb(230, 229, 229);
      border: none;
      outline: none;
      color: rgb(41, 41, 41);
      width: 100%;
      font-size: 1.2rem;
      padding: 0 1rem;
      margin-top: 2rem;
    }
    #btn {
      margin-top: 2rem;
      font-size: 1.3rem;
      background: gold;
      color: black;
      letter-spacing: 2px;
      padding: 0.5rem;
      cursor: pointer;
    }
    .check {
      text-align: center;
      margin-top: 2rem;
      font-size: 1.1rem;
      padding-bottom: 5rem;

      a {
        // border: 2px solid black;
        border-radius: 0.2rem;
        color: blue;
        padding: 0.4rem;
      }
    }
  }
}

@media (min-width: 900px) {
  .containerZ {
    margin-top: 0rem;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row;
    .images {
      padding: 2rem;
      box-shadow: 0px 20px 10px rgba(0, 0, 0, 0.3);
      border-radius: 20%;
      height: 100vh;
      margin-top: 4rem;
      width: 90%;
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        width: 100%;
        height: auto;
        margin-top: 2rem;
      }
    }
    form {
      display: flex;
      flex-direction: column;
      width: 80%;
      margin: 2rem;
      padding: 2rem;
      margin-top: 4rem;
      .text {
        h1 {
          font-size: 2rem;
          padding: 0.5rem 0 0rem;
          text-align: center;
        }
        p {
          font-size: 1.3rem;
          padding: 0;
          margin: 0;
          // margin-bottom: 2rem;
          color: #333;
        }
      }
    }
  }
}
