$black: black;
$white: white;
$gold: gold;
$gray: #333;
.WhyChooseUs {
  margin-top: 3rem;
  width: 100%;
  .container2 {
    .heading {
      text-align: center;
      padding: 0 2rem;
      h1 {
        padding: 1rem;
        font-size: 1.4rem;
        font-weight: bold;
      }
      p {
        font-size: 1.1rem;
        padding-bottom: 3rem;
        font-weight: 300;
        line-height: 1.5;
      }
    }
    .cardContainer {
      margin-top: 1rem;
      padding: 0 2rem;
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      .card {
        padding: 1rem;
        width: 260px;
        text-align: center;

        h3 {
          font-size: 1.2rem;
          padding: 1rem;
          font-style: italic;
          font-weight: 700;
        }
        p {
          font-size: 1rem;
          line-height: 2;
        }
      }
    }
  }
}
@media (min-width: 690px) {
  .WhyChooseUs {
    margin-top: 3rem;
    .container {
      .heading {
        text-align: center;
        padding: 0 2rem;
        h1 {
          padding: 1.5rem;
          font-size: 1.4rem;
        }
        p {
          font-size: 1.5rem;
          padding: 0rem 6rem 3rem;
          font-weight: 300;
          line-height: 1.5;
        }
      }
      .cardContainer {
        margin-top: 1rem;
        padding: 0 2rem;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        .card {
          padding: 1rem;
          width: 300px;
          text-align: center;

          h3 {
            font-size: 1.2rem;
            padding: 1rem;
          }
          p {
            font-size: 1.1rem;
            line-height: 2;
          }
        }
      }
    }
  }
}
