$black: black;
$white: white;
$gold: gold;
$gray: #333;
.quality {
  .container2 {
    .content {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin-top: 3rem;
      h1 {
        text-align: center;
        font-size: 1.5rem;
        padding: 1rem 0 0;
        font-weight: 600;
      }
      p {
        font-size: 1.2rem;
        text-align: center;
        padding: 0.3rem;
        font-weight: 500;
        line-height: 1.5;
      }
      .actions {
        margin: 0 1rem;
        .btn {
          background: $gold;
          padding: 1rem 2rem;
          border: none;
          font-size: 1.2rem;
          text-transform: uppercase;
          margin: 0.6rem 0;
          width: 100%;
        }
      }
      img {
        background: none;
        border-radius: 0%;
        width: 300px;
        height: auto;
        margin-top: 3rem;
        transition: 0.3s ease;
        &:hover {
          transform: scale(1.1);
        }
      }
    }
  }
}
@media (min-width: 690px) {
  .quality {
    .container2 {
      .content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: 3rem;
        h1 {
          text-align: center;
          font-size: 1.5rem;
          padding: 1rem 0 0;
          font-weight: 600;
        }
        p {
          font-size: 1.3rem;
          text-align: center;
          padding: 0.3rem 20%;
          font-weight: 500;
          line-height: 1.5;
        }
        .actions {
          display: flex;
          justify-content: space-between;
          margin-top: 2rem;
          .btn {
            background: $gold;
            padding: 1rem;
            border: none;
            font-size: 1.2rem;
            text-transform: uppercase;
            margin: 0.6rem 3rem;
            width: 300px;
          }
        }
        img {
          width: 450px;
          margin-top: 3rem;
          transition: 0.3s ease;
          &:hover {
            transform: scale(1.1);
          }
        }
      }
    }
  }
}
