$black: black;
$white: white;
$gold: gold;
$gray: #333;
.footer {
  margin-top: 3rem;
  .container2 {
    background: $black;
    .list {
      display: flex;
      text-align: center;
      flex-direction: column;

      li {
        color: white;
        display: flex;
        flex-direction: column;
        margin: 1rem 0;
        h5 {
          font-size: 1.1rem;
          padding: 0.6rem;
          color: gold;
          text-decoration: underline;
        }
        a {
          color: $white;
          font-size: 1.1rem;
          text-decoration: none;
          margin: 0.5rem 0;
          transition: 0.3s ease;
          &:hover {
            color: gold;
            text-decoration: underline;
          }
        }
        p {
          font-size: 1.2rem;
          padding: 0.4rem;
        }
      }
    }
  }

  .warning {
    padding: 0 1rem;
    margin-bottom: 4rem;
    h3 {
      color: #333;
      text-align: center;
      padding: 2rem 0 1rem;
      font-size: 1rem;
      text-transform: uppercase;
    }
    .mainred {
      color: rgba(255, 0, 0, 0.938);
      margin-bottom: 0.5rem;
    }
    p {
      font-size: 0.9rem;
      text-align: center;
      font-weight: bold;
      color: #333;
    }
  }
}

@media (min-width: 636px) {
  .footer {
    .container2 {
      .list {
        display: flex;
        text-align: left;
        justify-content: space-evenly;
        flex-direction: row;

        li {
          color: white;
          display: flex;
          flex-direction: column;
          margin: 1rem 3rem;
          h5 {
            font-size: 1.2rem;
            padding: 0.6rem;
            color: gold;
            text-decoration: underline;
          }
          a {
            color: $white;
            font-size: 1.1rem;
            text-decoration: none;
            margin: 0.5rem 0.6rem;
          }
          p {
            font-size: 1.2rem;
            padding: 0.4rem;
          }
        }
      }
    }
  }
}
