.accountType {
  margin-top: 4rem;
  background: url("../images/trading.jpg") rgba(0, 0, 0, 0.9);
  background-size: cover;
  background-position: center;
  background-blend-mode: overlay;
  clip-path: polygon(0 2%, 100% 0, 100% 90%, 0% 100%);
  padding: 3rem 1rem 10rem ;
  height: auto;
  .container2 {
    text-align: center;

    .heading {
      font-size: 1.7rem;
      font-weight: bold;
      position: relative;
      text-transform: uppercase;
      color: gold;
    }
    p {
      font-size: 1rem;
      padding: 1rem ;
      font-weight: 500;
      line-height: 1.5;
      color: white;
    }
  }
}

@media (min-width: 690px) {
  .accountType {
    margin-top: 5rem;
    .container {
      text-align: center;

      .heading {
        font-size: 1.7rem;
        position: relative;
        text-transform: uppercase;
      }
      p {
        padding: 1rem 6rem 2rem;
        font-size: 1.5rem;
      }
    }
  }
}
