* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.containerZ {
  margin-top: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.containerZ .images {
  padding: 2rem;
}
.containerZ .images img {
  width: 200px;
  height: auto;
}
.containerZ form {
  display: flex;
  flex-direction: column;
  width: 80%;
}
.containerZ form .text h1 {
  font-size: 2rem;
  padding: 0.5rem 0 0rem;
  text-align: center;
}
.containerZ form .text p {
  font-size: 1.3rem;
  margin-bottom: 2rem;
  color: #333;
  text-align: center;
}
.containerZ form .label {
  font-size: 1.3rem;
  margin-top: 1rem;
  padding: 0.5rem 0;
}
.containerZ form .input {
  height: 3rem;
  background: rgb(230, 229, 229);
  border: none;
  outline: none;
  width: 100%;
  font-size: 1.2rem;
  padding: 0 1rem;
}
.containerZ form select {
  height: 3rem;
  background: rgb(230, 229, 229);
  border: none;
  outline: none;
  color: rgb(41, 41, 41);
  width: 100%;
  font-size: 1.2rem;
  padding: 0 1rem;
  margin-top: 2rem;
}
.containerZ form #btn {
  margin-top: 2rem;
  font-size: 1.3rem;
  background: gold;
  color: black;
  letter-spacing: 2px;
  padding: 0.5rem;
  cursor: pointer;
}
.containerZ form .check {
  text-align: center;
  margin-top: 2rem;
  font-size: 1.1rem;
  padding-bottom: 5rem;
}
.containerZ form .check a {
  border-radius: 0.2rem;
  color: blue;
  padding: 0.4rem;
}

@media (min-width: 900px) {
  .containerZ {
    margin-top: 0rem;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row;
  }
  .containerZ .images {
    padding: 2rem;
    box-shadow: 0px 20px 10px rgba(0, 0, 0, 0.3);
    border-radius: 20%;
    height: 100vh;
    margin-top: 4rem;
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .containerZ .images img {
    width: 100%;
    height: auto;
    margin-top: 2rem;
  }
  .containerZ form {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 2rem;
    padding: 2rem;
    margin-top: 4rem;
  }
  .containerZ form .text h1 {
    font-size: 2rem;
    padding: 0.5rem 0 0rem;
    text-align: center;
  }
  .containerZ form .text p {
    font-size: 1.3rem;
    padding: 0;
    margin: 0;
    color: #333;
  }
}/*# sourceMappingURL=Login&register.css.map */