.accountType {
  margin-top: 4rem;
  background: url("../images/trading.jpg") rgba(0, 0, 0, 0.9);
  background-size: cover;
  background-position: center;
  background-blend-mode: overlay;
  -webkit-clip-path: polygon(0 2%, 100% 0, 100% 90%, 0% 100%);
          clip-path: polygon(0 2%, 100% 0, 100% 90%, 0% 100%);
  padding: 3rem 1rem 10rem;
  height: auto;
}
.accountType .container2 {
  text-align: center;
}
.accountType .container2 .heading {
  font-size: 1.7rem;
  font-weight: bold;
  position: relative;
  text-transform: uppercase;
  color: gold;
}
.accountType .container2 p {
  font-size: 1rem;
  padding: 1rem;
  font-weight: 500;
  line-height: 1.5;
  color: white;
}

@media (min-width: 690px) {
  .accountType {
    margin-top: 5rem;
  }
  .accountType .container {
    text-align: center;
  }
  .accountType .container .heading {
    font-size: 1.7rem;
    position: relative;
    text-transform: uppercase;
  }
  .accountType .container p {
    padding: 1rem 6rem 2rem;
    font-size: 1.5rem;
  }
}/*# sourceMappingURL=AccountTypes.css.map */