.sidebar {
  height: calc(100vh - 50px);
  // flex: 1;
  ul{
    .title{
        color: rgba(102, 102, 102, 0.5);
        margin-top: .2rem;
        font-size: 0.9rem;
        margin-left: 1rem;
    }
}
}
.deposit {
  height: calc(100vh - 50px);
  // flex: 1;
}
.home {
  height: auto;
  // flex: 3;
}

.links {
  margin-top: 1.5rem;
  a {
    display: flex;
    align-items: center;    
  }
}

@media (min-width: "769px") {
  .sidebar {
    flex: 1;
    border-right: 2px solid rgba(102, 102, 102, 0.1);
    width: 500px;
    ul{
        .title{
            color: rgba(102, 102, 102, 0.3);
            margin-top: .2rem;
            font-size: 1.2rem;
            margin-left: 1rem;
        }
    }
  }
  .deposit {
    flex: 4;
  }
  .home {
    flex: 4;
  }
}
