/* @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;700;800&display=swap'); */
@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;0,900;1,300&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: "Roboto", sans-serif;
}

body{
  width: 100vw;
  /* padding-bottom: 5rem; */
}
button{
  cursor: pointer;
  transition: .3s;
}
button:hover{
  transform: scale(1.05);
}
a{
  text-decoration: none;
  color: black;
}
p{
  font-family: "Lato", sans-serif;
}
li{
  font-family: "Roboto", sans-serif;
  list-style-type: none;
}
.btn{
  transition: .3s;
  font-family: 'Lato', sans-serif;
}
.btn:hover{
  transform: scale(1.05);
}

 