.gold {
  margin-top: 3rem;
}
.gold .heading h1 {
  text-align: center;
  text-transform: uppercase;
  font-size: 1.6rem;
  padding: 1rem;
}
.gold .body {
  width: 100%;
}
.gold .body img {
  width: 100%;
}
.gold .body h4 {
  font-size: 1.1rem;
  color: black;
  text-align: center;
  padding-top: 1rem;
}
.gold .body p {
  font-size: 1.2rem;
  padding: 0.5rem 1rem;
}

@media (min-width: 800px) {
  .gold {
    margin-top: 4rem;
  }
}
@media (min-width: 1000px) {
  .gold {
    margin-top: 4rem;
  }
  .gold .heading h1 {
    text-align: center;
    text-transform: uppercase;
    font-size: 2.4rem;
    padding: 1rem;
  }
  .gold .body {
    width: 100%;
    display: flex;
    margin: 0 2rem;
    align-items: flex-start;
  }
  .gold .body img {
    width: 80%;
  }
  .gold .body .bodyCover {
    width: 100%;
    margin-right: 2rem;
  }
  .gold .body .bodyCover h4 {
    font-size: 1.1rem;
    color: black;
    text-align: center;
    padding-top: 1rem;
  }
  .gold .body .bodyCover p {
    font-size: 1.3rem;
    padding: 0.5rem 1.5rem;
  }
}/*# sourceMappingURL=Gold.css.map */