.gold {
  margin-top: 3rem;
  .heading {
    h1 {
      text-align: center;
      text-transform: uppercase;
      font-size: 1.6rem;
      padding: 1rem;
    }
  }
  .body {
    width: 100%;
    img {
      width: 100%;
    }
    h4 {
      font-size: 1.1rem;
      color: black;
      text-align: center;
      padding-top: 1rem;
    }
    p {
      font-size: 1.2rem;
      padding: 0.5rem 1rem;
    }
  }
}
@media (min-width: 800px) {
  .gold {
    margin-top: 4rem;
  }
}

@media (min-width: 1000px) {
  .gold {
    margin-top: 4rem;
    .heading {
      h1 {
        text-align: center;
        text-transform: uppercase;
        font-size: 2.4rem;
        padding: 1rem;
      }
    }
    .body {
      width: 100%;
      display: flex;
      margin: 0 2rem;
      // justify-content: space-between;
      align-items: flex-start;
      img {
        width: 80%;
      }
      .bodyCover {
        width: 100%;
        margin-right: 2rem;
        h4 {
          font-size: 1.1rem;
          color: black;
          text-align: center;
          padding-top: 1rem;
        }
        p {
          font-size: 1.3rem;
          padding: 0.5rem 1.5rem;
        }
      }
    }
  }
}
