.step {
  background: url("../images/pc.png") rgba(20, 0, 0, 0.945);
  background-position: center;
  background-size: cover;
  background-blend-mode: overlay;
  margin-top: 3rem;
}
.step .title {
  text-align: center;
  color: gold;
  text-transform: uppercase;
  padding-bottom: 2rem;
  font-size: 1.5rem;
  padding-top: 1.3rem;
}
.step .container2 {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.step .container2 .content {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-top: 1.5rem;
  padding: 3rem 0;
  cursor: pointer;
  transition: 0.3s ease;
}
.step .container2 .content:hover {
  transform: scale(1.02);
}
.step .container2 .content .circle {
  background: gold;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: absolute;
  top: -20px;
  font-size: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
}
.step .container2 .content .card {
  background-color: white;
  border-radius: 1.3rem;
  padding: 2rem 2rem 2rem 0.8rem;
  width: 300px;
}
.step .container2 .content .card h1 {
  font-weight: 600;
  font-size: 1.5rem;
  text-align: center;
  padding: 1rem;
}
.step .container2 .content .card p {
  font-weight: 500;
  font-size: 1.3rem;
  text-align: center;
}/*# sourceMappingURL=Steps.css.map */