.WhyChooseUs {
  margin-top: 3rem;
  width: 100%;
}
.WhyChooseUs .container2 .heading {
  text-align: center;
  padding: 0 2rem;
}
.WhyChooseUs .container2 .heading h1 {
  padding: 1rem;
  font-size: 1.4rem;
  font-weight: bold;
}
.WhyChooseUs .container2 .heading p {
  font-size: 1.1rem;
  padding-bottom: 3rem;
  font-weight: 300;
  line-height: 1.5;
}
.WhyChooseUs .container2 .cardContainer {
  margin-top: 1rem;
  padding: 0 2rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.WhyChooseUs .container2 .cardContainer .card {
  padding: 1rem;
  width: 260px;
  text-align: center;
}
.WhyChooseUs .container2 .cardContainer .card h3 {
  font-size: 1.2rem;
  padding: 1rem;
  font-style: italic;
  font-weight: 700;
}
.WhyChooseUs .container2 .cardContainer .card p {
  font-size: 1rem;
  line-height: 2;
}

@media (min-width: 690px) {
  .WhyChooseUs {
    margin-top: 3rem;
  }
  .WhyChooseUs .container .heading {
    text-align: center;
    padding: 0 2rem;
  }
  .WhyChooseUs .container .heading h1 {
    padding: 1.5rem;
    font-size: 1.4rem;
  }
  .WhyChooseUs .container .heading p {
    font-size: 1.5rem;
    padding: 0rem 6rem 3rem;
    font-weight: 300;
    line-height: 1.5;
  }
  .WhyChooseUs .container .cardContainer {
    margin-top: 1rem;
    padding: 0 2rem;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .WhyChooseUs .container .cardContainer .card {
    padding: 1rem;
    width: 300px;
    text-align: center;
  }
  .WhyChooseUs .container .cardContainer .card h3 {
    font-size: 1.2rem;
    padding: 1rem;
  }
  .WhyChooseUs .container .cardContainer .card p {
    font-size: 1.1rem;
    line-height: 2;
  }
}/*# sourceMappingURL=WhychooseUs.css.map */