.about {
  .container2 {
    margin-top: 3rem;
    .headings {
      background: url("../images/investor.jpg") rgba(0, 15, 29, 0.959);
      background-position: center;
      background-size: cover;
      background-blend-mode: overlay;
      padding: 2rem;
      text-align: center;
      font-size: 1rem;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 4rem;
     
      h1 {
        text-align: center;
        position: relative;
        font-size: 1.5rem;
        color: white;
        position: relative;
        &::before {
          content: "";
          background: gold;
          width: 175px;
          height: 5px;
          position: absolute;
          bottom: 0px;
          left: 50%;
          transform: (translate(-50%, 50%));
        }
      }
      
    }
    .content {
      text-align: center;
      padding: 0;
      .card {
        .title {
          font-size: 1.3rem;
          text-transform: uppercase;
        }
        p {
          line-height: 1.6;
          padding: 1rem;
          font-weight: 400;
          text-align: left;
        }
      }
      
    }
  }
}
